@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

.language-list {
  margin-left: 1em;
  margin-bottom: 2em;
  font-size: 15px;
  
}



.btn-demo {
  font-size: 14px;
  min-width: 120px !important;
  padding: .4em 1em;
  height: 35px;
  margin: 1.7em .5em 1.7em 0;
  border-radius: 25px;
  border: 0;
  background: $blue;
  color: $white;
  &:hover {
      background-color: $blue-hover;
      border-color: $blue-hover;
  }
  &:active {
      border-color: $white;
  }
}

.small {
position: absolute;
width: 100%;
background: $dark-blue;
z-index: 2000;
}
@media (max-width: 1390px) {
  a.nav-link  {
      font-size: 14px;
      padding: 29px 10px !important;
  }
  a.nav-link.language {
      font-size: 14px;
  }
  .navbar-brand {
      padding: .7em 0;
  }
  .header-logo {
      height: 45px;
  }
}


@media (max-width: 1200px) {
  .nav-list {
      display: none;
  }
  .navigation {
      display: inline;
  }
  a.nav-link.language {
      font-size: 1.2em;
  }
  .navbar-brand {
      padding: .7em 0;
  }
  .header-logo {
      height: 50px;
  }
}
@media screen and (min-width:900px) {
.overlay nav {
   
  font-size: 1.2em;
  font-weight: 400;
  text-align: center;
}
}
@media (max-width: 1000px) and (max-height: 500px){
  .overlay {
      overflow: scroll;
    }
  }
























