$timing:.3s;


.dropdown-el {
    
  min-height:95vh;
  margin:0;
  padding:0;
  border-bottom: 5vh solid $blue;
  color: $white;
  font-size: 20px;
  font-weight: 400;

  width: 100%;
  position: relative;
  display: inline-block;
  margin-right: 3em;
  min-height: 50px;
  max-height:2em;
  overflow:hidden;
  top: .5em;  
  cursor: pointer;
  text-align: left;

  ;
  
  outline: none;


  background-color: $dark;
  border-top: 2px solid $dark-blue;
  border-bottom: 2px solid $dark-blue;
  transition: $timing all ease-in-out;
  input:focus + label {
    background: #def;
  }
  input {
    width: 1px;
    height: 1px;
    display: inline-block;
    position: absolute;
    opacity: 0.01;
 
  }
  label {

    display:block;
    height: 2em;
    line-height: 2em;
    padding-left: 2em;
    padding-right: 3em;
    cursor: pointer;
    position: relative;
    transition: $timing color ease-in-out;  
    &:nth-child(2) {
      margin-top: 2em;

    }
  }
  input:checked + label {
    display:block;
    border-top: none;
    position: absolute;
    top: 0;
    width: 100%;

    &:nth-child(2) {
      margin-top: 0;
      position: relative;
    }
  }
  
  &::after {
    content:"";
    position: absolute;
    right: 0.8em;
    top: 0.9em;
    border: .3em solid $blue;
    border-color: $blue transparent transparent transparent;
    transition: .4s all ease-in-out;
  }
  &.expanded {
 
    background: $dark-blue;
    padding: 0;
    box-shadow: rgba(0, 0, 0, 0.1) 3px 3px 5px 0px;
    max-height:50em;
    
    label {

      &:hover {
        color:$blue;
      }
    }
    input:checked + label {
      color: RED;
    }
    
    &::after {
      transform: rotate(-180deg);
      top:.55em;
    }
  }
}
